.item{
  &-wrapper{
    margin-top: 26px;
    padding-bottom: 84px;
  }
  &-block + &-block{
    margin-top: 12px;
  }
  &-block{
    background-color: #fff;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
    border-radius: 6px;
    padding: 35px 25px;
    &_img{
      padding: 15px;
    }
    &__title{
      font-size: 22px;
      font-weight: 500;
    }
  }
  &-slider-img{
    width: 100%;
    height: 480px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 9px;
    }
  }
  &-wrapper{
    &__grid{
      display: grid;
      grid-template-columns: calc(100% - 315px) 315px;
      gap: 14px;
    }
    &__grid + &__grid{
      margin-top: 10px;
    }
  }
  &-form{
    &__wrapper{
      background-color: #fff;
      padding: 25px;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
    }
    &__input{
      .text-input{
        height: 42px;
        font-size: 15px;
      }
    }
    &__input + &__input{
      margin-top: 10px;
    }
    &__footer{
      margin-top: 15px;
    }
    &__submit{
      height: 42px;
      width: 100%;
    }
    &__title{
      font-size: 18px;
      font-weight: 600;
    }
    &__content{
      margin-top: 20px;
    }
  }
}

@media screen and (min-width: 991px) {
  .item{
    &-form{
      &__wrapper{
        border-radius: 6px;
        padding: 40px 26px;
      }
      &__title{
        font-size: 18px;
        text-align: center;
      }
    }
  }
}